          <span class="hljs-attr">url</span>=<span class="hljs-string">"https://dev.virtualearth.net"</span>
          <span class="hljs-attr">bmKey</span>=<span class="hljs-string">"AgcbDCAOb9zMfquaT4Z-MdHX4AsHUNvs7xgdHefEA5myMHxZk87NTNgdLbG90IE-"</span>
          <span class="hljs-attr">mapStyle</span>=<span class="hljs-string">"Aerial"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-bingmaps</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-terrain-arcgis-tiled-elevation</span> <span class="hljs-attr">:url</span>=<span class="hljs-string">"terrainUrl"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-terrain-arcgis-tiled-elevation</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">terrainUrl</span>: <span class="hljs-string">'https://elevation3d.arcgis.com/arcgis/rest/services/WorldElevation3D/Terrain3D/ImageServer'</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">var</span> target = <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">300770.50872389384</span>, <span class="hljs-number">5634912.131394585</span>, <span class="hljs-number">2978152.2865545116</span>)
